.titleWrapper h2,.titleWrapper h6{display:flex;flex-direction:row;justify-content:center;align-items:center;gap:0}.registerForm{width:400px;margin:100px auto;padding:2rem;border-radius:10px}.toggleButtons{display:flex;flex-direction:row;justify-content:center;align-items:center;gap:1rem;margin-bottom:1rem}.toggleButtons button{background:none;border:none;padding:8px 24px;border-radius:100px;cursor:pointer;color:#444;transition:background .2s,color .2s}.toggleButtons .active{background:var(--primary);color:#fff}.inputGroup{display:flex;flex-direction:row;justify-content:center;align-items:center;gap:.5rem;align-items:center;margin-bottom:1rem}.input{width:100%;padding:.5rem;border-radius:6px;border:1px solid #ccc;background-color:var(--white);color:var(--black);margin-bottom:1rem}.input.light{padding:.5rem;border-radius:6px;border:1px solid #ccc;background-color:var(--white);color:var(--black)}.input.dark{padding:.5rem;border-radius:6px;border:1px solid #ccc;background-color:var(--dark2);color:var(--white)}.loginButton{width:100%;padding:.75rem;background-color:var(--primary);color:var(--white);border:none;border-radius:6px;cursor:pointer}.registerButton{width:100%;padding:.75rem;background-color:var(--primary);color:var(--white);border:none;border-radius:100px;cursor:pointer}.registerPrompt{margin-top:1rem;text-align:center}.select{display:flex;flex-direction:row;justify-content:center;align-items:center;gap:0;width:100%;margin-bottom:1rem;padding:.5rem;border-radius:8px;border:1px solid #ccc}.select.light{background-color:var(--white);color:var(--black)}.select.dark{background-color:var(--dark2);color:var(--white)}